const app = getApp();
export default {
    data() {
        return {
            showLoginBox: 1,
            userInfo: {},
            fullLogin: 0,
            parentObj: "",
            env: ""
        };
    },
    props: {
        type: {
            type: String,
            default: "login"
        },
        showLogin: {
            type: Number,
            default: "1"
        },
        onlyLogin: {
            type: Number,
            default: 0
        },
        loadData: function(type) {
            this.$emit("pLoadData");
        }
    },
    async created() {
        // this.userInfo = uni.getStorageSync("userInfo");
        if (this.getIsWxClient()) {
            this.env = "weixin";
        } else {
            this.env = "h5";
        }

        const session_key = uni.getStorageSync("session_key") || "";
      
        if(session_key){
            this.showLoginBox = 0;
        }

        if (!session_key) {
            const code = this.getQueryString("code") || "";
            if (code) {
                const res = await app.req("special/login/getCode", {
                    code
                });

                if (res.data.code == 0) {
                    uni.setStorageSync("session_key", res.data.auth);
                    this.showLoginBox = 0;
                    if (this.parentObj == "loadData") {
                        this.$emit("pLoadData");
                    }
                } else {
                    this.$api.msg(res.data.msg);
                }
            }
        }

        uni.$on('login_hide', () => {
            this.showLoginBox = 0
        })

        console.log(this.userInfo, this.showLoginBox, this.showLogin)
    },
    methods: {
        async login_wx() {
            const res = await app.req("special/login/loginWx");
            if (res.data.code == 0) {
                const url = res.data.data.url;
                window.location.href = url;
            } else {
                this.$api.msg(res.data.msg);
            }
        },
        async login_phone() {
            uni.navigateTo({
                 url: '/pages/public/login_phone'
            });
        },
        getQueryString(name) {
            var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)");
            var r = window.location.search.substr(1).match(reg);
            if (r != null) return unescape(r[2]);
            return null;
        },
        getIsWxClient() {
            var ua = navigator.userAgent.toLowerCase();
            if (ua.match(/MicroMessenger/i) == "micromessenger") {
                return true;
            }
            return false;
        },
        toggleMask(obj) {
            this.showLoginBox = 1;
            this.fullLogin = 1;
            this.parentObj = obj;
        },
        getUserInfo(res) {
            var _this = this;
            if (!res.detail.iv) {
                uni.showToast({
                    title: "您取消了授权,登录失败",
                    icon: "none"
                });
                return false;
            }
            uni.login({
                provider: "weixin",
                success: function(loginRes) {
                    uni.getUserInfo({
                        provider: "weixin",
                        withCredentials: true,
                        success: function(infoRes) {
                            uni.request({
                                url: "https://td.clmsj.com/clmsj/login/index", // http://bull.hnw.net/index/bull/client_wash_info
                                data: {
                                    encrypt_data: encodeURIComponent(
                                        infoRes.encryptedData
                                    ),
                                    iv: encodeURIComponent(infoRes.iv),
                                    code: loginRes.code,
                                    type: 2 //type   1专业版  2零售版
                                },
                                method: "POST",
                                success: res => {
                                    if (res.data.code == 0) {
                                        uni.setStorageSync(
                                            "session_key",
                                            res.data.auth
                                        );
                                        uni.setStorageSync(
                                            "userInfo",
                                            res.data.data.user
                                        );
                                        _this.showLoginBox = 0;
                                        if (_this.parentObj == "loadData") {
                                            _this.$emit("pLoadData");
                                        }
                                    } else {
                                        uni.showToast({
                                            icon: "none",
                                            title: "请重新获取信息..."
                                        });
                                    }
                                }
                            });
                        },
                        fail: function(infoRes) {
                            console.log("infoRes=" + JSON.stringify(infoRes));
                        }
                    });
                }
            });
        },
        hideLoginBox() {
            this.showLoginBox = 0;
        },
        eventClick() {
            this.$emit("eventClick");
        }
    }
};
